<template>
  <a-layout style="min-height: 100vh">
    <a-layout-sider @collapse="collapseChange" v-model:collapsed="collapsed" collapsible>
      <div class="logo">{{logoTitle}}</div>
      <a-menu @openChange="openChangeHandler" @select="selectedChangeHandler" theme="dark" v-model:selectedKeys="selectedKeys" v-model:openKeys="openKeys" mode="inline">
        <!-- 遍历生成菜单列表区域 -->
        <a-sub-menu :key="item.id" v-for="item in menusList">
          <template #title>
            <span>
              <!-- 一级菜单图标 -->
              <user-outlined v-if="item.id==125" />
              <KeyOutlined v-if="item.id==103" />
              <InsertRowBelowOutlined v-if="item.id==101" />
              <ShoppingOutlined v-if="item.id==102" />
              <ControlOutlined v-if="item.id==145" />
              <span>{{item.authName}}{{item.id}}</span>
            </span>
          </template>
          <!-- 二级子菜单区域 -->
          <a-menu-item :key="value.id" v-for="value in item.children">
            <!-- 定义路由 -->
            <router-link :to="'/home/'+value.path">{{value.authName}}{{value.id}}</router-link>
          </a-menu-item>
          <!-- 二级子菜单区域   end -->
        </a-sub-menu>
      </a-menu>
    </a-layout-sider>
    <a-layout>
      <!-- 这是头部区域 -->
      <a-layout-header class="header">
        <span style="margin-right:30px">{{currentUsername}}</span>
        <a-button @click="logoutHandler">退出</a-button>
      </a-layout-header>
      <a-layout-content style="margin: 0 16px">
        <!-- 面包屑导航区域 -->
        <a-breadcrumb style="margin: 16px 0">
          <a-breadcrumb-item v-for="(item,index) in breadcrumbPath" :key="index">
                {{item}}
          </a-breadcrumb-item>
        </a-breadcrumb>
        <!-- 显示具体功能组件的具体区域 -->
        <div :style="{ padding: '24px', background: '#fff', minHeight: '360px' }">
            <router-view></router-view>
          </div>
          <!-- end -->
      </a-layout-content>
      <a-layout-footer style="text-align: center">
        Ant Design ©2018 Created by Ant UED
      </a-layout-footer>
    </a-layout>
  </a-layout>
</template>
<script>
// 导入图标组件
import { UserOutlined, KeyOutlined, InsertRowBelowOutlined, ShoppingOutlined, ControlOutlined } from '@ant-design/icons-vue'
import { message } from 'ant-design-vue'
import { defineComponent, ref, getCurrentInstance, onBeforeMount, reactive } from 'vue'
export default defineComponent({
  components: {
    // 祖册图标组件
    UserOutlined,
    KeyOutlined,
    InsertRowBelowOutlined,
    ShoppingOutlined,
    ControlOutlined
  },
  setup () {
  // 左侧展开是上面标题的内容显示
    const logoTitle = ref('XXX后台管理系统')
    const collapseChange = a => {
      if (a) {
        logoTitle.value = 'XXX'
      } else {
        logoTitle.value = 'XXX后台管理系统'
      }
    }
    // 找到当前的实例
    const { proxy } = getCurrentInstance()
    // 显示当前登陆的用户名
    const currentUsername = window.sessionStorage.username
    // 退出登陆功能
    const logoutHandler = () => {
      window.sessionStorage.clear()
      message.success('退出成功')
      proxy.$router.push('/login')
    }
    // 发送axios获取菜单数据
    const menusList = ref()
    onBeforeMount(async () => {
      const { data } = await proxy.$http.get('menus')
      menusList.value = data
    })
    const openKeys = ref([window.sessionStorage.openKey - 0])
    const selectedKeys = ref([window.sessionStorage.selectedKey - 0])
    // 菜单切换触发的事件
    const selectedChangeHandler = ({ keyPath }) => {
      window.sessionStorage.openKey = keyPath[0]
      window.sessionStorage.selectedKey = keyPath[1]
    }
    // 控制一级菜单不能同时打开多个
    const openChangeHandler = (arr) => {
      openKeys.value = [arr[arr.length - 1]]
    }
    // 定义面包屑地址
    const breadcrumbPath = reactive({
      pathOne: 'home',
      pathTwo: ''
    })
    // 设置路由守卫
    proxy.$router.afterEach((to, from) => {
      if (to.path == '/home') breadcrumbPath.pathTwo = ''
      const pathArr = to.path.split('/')
      const pathObj = {}
      if (pathArr.length == 2) {
        pathObj.fatherPath = pathArr[1]
      } else {
        pathObj.fatherPath = pathArr[1]
        pathObj.sonPath = pathArr[2]
        breadcrumbPath.pathTwo = pathArr[2]
      }
    })
    return {
      menusList,
      logoutHandler,
      collapseChange,
      logoTitle,
      collapsed: ref(false),
      // openKeys控制展开哪个一级菜单
      openKeys,
      // selectedKeys是控制当前选中哪个菜单
      selectedKeys,
      currentUsername,
      selectedChangeHandler,
      openChangeHandler,
      breadcrumbPath
    }
  }
})
</script>
<style>
.logo {
  overflow: hidden;
  height: 32px;
  margin: 16px;
  line-height: 32px;
  text-align: center;
  color: #fff;
  background: rgba(255, 255, 255, 0.3);
}
.header{
  text-align: right;
  padding: 0;
  padding-right: 50px;
  background: #fff;
}
.site-layout .site-layout-background {
  background: #fff;
}
[data-theme='dark'] .site-layout .site-layout-background {
  background: #141414;
}
</style>
